@import url('https://fonts.googleapis.com/css2?family=Raleway:wght@300&display=swap');

:root {
    --ukuran-huruf:1vw;
    --warna-section1:white;
}

body {
    font-family:"Raleway" sans-serif;
    font-size: 1.2em;
}

.container {
    position: relative;
    padding:0;
    margin:0;
    box-sizing: content-box;
}

/* section 1 */

.section1 {
    display: flex;
    flex-flow: column nowrap;
    background-position: center center;
    background-image: url(./assets/img/bg-loop-01.jpg);
    background-repeat: no-repeat;
    background-attachment: fixed;
    min-height: 500px;
    height: 100vh;
    color:white;
    user-select: none;
}

.inft-loop {
    position: fixed;
    width: 100%;
    display: flex;
}

.inft-nav {
    width: 100%;
    display: flex;
    justify-content: space-between;
    padding:0 3em 0 3em;
    letter-spacing: 1.1px;
    z-index: 1000;
    background-color: transparent;
    color:#fff;
}

.inft-nav-bg-white {
    display: flex;
    justify-content: space-between;
    letter-spacing: 1.1px;
    z-index: 1000;
    background-color:#369;
    transition: 1s ease;
}

.if-left {
    flex:1 1 30%;
    display: flex;
    flex-flow: row wrap;
    align-items: center;
    align-content: center;
    justify-content: space-between;
}

.if-left .fl-text {
    font-size: 1.75rem;
    font-weight:bold;
}

.if-left .btn-bars {
    display: none;
    padding:8px 12px 8px 12px;
    border:1px solid #fff;
    background-color: transparent;
    border-radius: 5px;
}

.if-left .size-bars-btn {
    font-size: 1.5em;
    color: whitesmoke;
}

.if-right {
    flex:1 1 70%;
    display: flex;
    flex-flow: row wrap;
    align-content: space-around;
    justify-content: space-around;
    align-items: center;
    padding:1em;
}

.if-right .item {
    flex:1 1 auto;
    font-size: 1.2rem;
    text-decoration:transparent;
    text-align: right;
    font-weight: 700;
    color:#fff;
}


.inft-desc, .inft-arrows {
    width: 100%;
    height: 100%;
    display: flex;
    flex-flow: column;
    align-items: center;
    justify-content: center;
    align-content: space-between;
    cursor:default;
    letter-spacing: 1px;
}

.inft-desc .desc-sl {
    font-size: 4rem;
    padding:1.2em 0 0 0;
}

.inft-desc .desc-text-one,.desc-text-two {
    font-size: 3vh;
}

.inft-arrows .arrows-p {
    background-color: #357;
    padding:1em 2em;
}

.inft-arrows .arrows-p-size {
    font-size:3vh;
}

/* section 1 */

/* section 2 */

.section2 {
    display: flex;
    flex-flow: column;
    align-items:flex-start;
    padding:0 3em 0 3em;
    margin: 0 0 3vh 0;
    height: auto;
    user-select: none;
}

.sect2-desc-one {
    display: flex;
    flex-flow: column nowrap;
}

.sect2-desc-one .wwd-title {
    font-size: 2em;
    padding:3em 0 0 0;
    margin:0;
    letter-spacing: 0.3px;
}

.sect2-desc-one .wwd-desc {
    text-align: justify;
    font-size: 3vh;
    letter-spacing: 0.25px;
}

.sect2-desc-two {
    display: flex;
    flex-direction: column;
    width: 100%;
}

.sect2-desc-two .market-feature {
    display: flex;
    flex-flow: row wrap;
}

.market-feature .item {
    flex:1 1 45%;
    display: flex;
    align-items: stretch;
    text-align: justify;
    padding:0.5em;
    font-size: 3vh;
}

.market-feature .item .item-icon {
    flex:1 1 20%;
    display: flex;
    justify-content: center;
    padding:0.25em 0 0.25em 0;
}

.market-feature .item .item-desc {
    flex:1 1 80%;
    display: flex;
    flex-flow: column;
    justify-content: flex-start;
    padding:0.25em 0 0.25em 0;
}

.market-feature .item .h2-item {
    margin: 0;
    color:#368;
}

.market-feature .btn-detail {
    display: block;
    max-width: 40%;
    padding:0.7em 1em 0.7em 1em;
    margin: 1.2em 0 1.2em 0;
    color:#fff;
    background-color: #369;
    font-size: 1em;
}

/* section 2 */

  /* section 3 */

 .section3 {
    height: 100vh;
    display: flex;
    flex-flow: column nowrap;
    align-items: center;
    justify-content: space-around;
    background-image: url(./assets/img/infinite-loop-02.jpg);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    background-attachment: fixed;
    padding:0;
    user-select: none;
 }

 .sct3-one {
     display: flex;
     flex-flow: column;
     width: 100%;
 }

 .sct3-one .testimonial-area {
     display: flex;
     flex-flow: column nowrap;
     align-items: center;
     align-content: space-around;
     justify-content:center;
     color:#fff;
 }

 .sct3-one .ta-title {
    font-size: 6vh;
 }

 .sct3-one .ta-desc {
    max-width: 50%;
    text-align: center;
    font-size: 3vh;
    margin: 0;
 }

 .sct3-two {
     display: flex;
     flex-flow: column;
 }

 .sct3-two .testimonial-board {
     display: flex;
     align-items: center;
     justify-content: space-evenly;
     padding:1em;
 }

 .sct3-two .testimonial-board .item {
    display: flex;
    flex-flow: column nowrap;
    align-items: center;
    justify-content: center;
    color: white;
    max-width: 20%;
    text-align: justify;
    flex: 1 1 15%;
 }

 .item .item-rounded {
     width: 40%;
     max-width: 50%;
     border-radius: 50%;
     border:3px solid #369;
 }

  /* section 3 */

  /* section 4 */

  .section4 {
      display: flex;
      flex-flow: column;
      align-items: center;
      justify-content:space-around;
      height: 100vh;
  }

  .section4 .row4 {
      display: flex;
      flex-flow: column;
  }

  .section4 .sect4-one {
    flex: 1 1 100%;
    max-width: 100%;
    display: flex;
    flex-flow: column;
    align-items: center;
    justify-content: center;
    padding:2em 1em;
  }

  .sect4-one .s40-title {
      font-size: 6vh;
  }

  .sect4-one .s40-desc {
      max-width: 60%;
      text-align: center;
      font-size: 3vh;
      margin: 0;
  }

  .section4 .sect4-two {
      flex: 1 1 100%;
      max-width: 100%;
      display: flex;
      flex-flow: column;
      padding:2em 1em;
  }

  .section4 .gallery-item {
      display: flex;
      flex-flow: row nowrap;
      align-items: center;
      justify-content: space-evenly;
  }

  .gallery-item .item {
    flex:1 1 auto;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
  }

  .gallery-item img {
      width: 100%;
      height: auto;
      z-index: -1;
  }

  .gallery-item .gl-container {
      position: relative;
      max-width: 270px;
      width: 100%;
      z-index: -1;
  }

  .gallery-item .gi-desc {
      position: absolute;
      width: 100%;
      height: auto;
      left: 0;
      bottom: 0;
      text-align: center;
      color:white;
      text-align: left;
      background-color: rgba(0, 0, 0, .5);
      text-transform: uppercase;
  }

  .gallery-item .desc-gl {
      font-size: 1em;
      font-weight: 500;
  }

  /* section 4 */


  /* section 5 */


  .section5 {
      display: flex;
      flex-flow: column wrap;
      align-items: center;
      justify-content: flex-start;
      background-image: url(./assets/img/infinite-loop-03.jpg);
      background-repeat: no-repeat;
      background-size: cover;
      background-position: center center;
      background-attachment: fixed;
      color:#fff;
      height: auto;
  }

  .section5 .row5 {
      display: flex;
      flex-wrap: wrap;
      padding: 10vh 0;
      max-width: 75%;
  }

  .section5 .cols-1 {
      flex:0 0 100%;
      max-width: 100%;
      display: flex;
      flex-flow: column wrap;
      align-items: center;
      justify-content: center;
  }

  .section5 .h2-cols-1 {
      min-width: 50%;
      font-size: 5vh;
      text-align: center;
      margin-bottom: 2rem;
  }

  .section5 .p-cols-1 {
      max-width: 75%;
      text-align: center;
      font-size: 3vh;
  }

  .section5 .cols-2 {
      flex:1 1 100%;
      max-width: 100%;
      display: flex;
      flex-flow: row wrap;
      padding:1em;
  }

  .section5 .form-contact {
    flex:1 1 40%;
    max-width: 50%;
    display: flex;
    flex-flow: column;
    align-content: flex-start;
    align-content: flex-start;
    justify-content: flex-start;
    padding:1em;
    font-size: 3vh;
  }

  .section5 .input-area {
      border: 3px solid #fff;
      background-color: transparent;
      border-radius:10px;
      padding:0.75em 1em;
      margin: 1em 0;
      color: white;
      font-size: 2.25vh;
   }

   .input-area::placeholder {
       color:white;
   }

   .section5 .t-input {
       overflow: auto;
       resize: vertical;
   }

   .section5 .btn-submit-contact {
       padding:1em;
       width: 100%;
       max-width: 50%;
       background-color: #369;
       font-size: 3vh;
       color:#fff;
       font-weight: 500;
       border:0;
   }

   .section5 .btn-submit-contact:hover {
       background-color: #38B;
       cursor: pointer;
   }

   .section5 .form-desc {
        flex:1 1 40%;
        max-width: 50%;
        display: flex;
        flex-flow: column;
        padding:1em;
        font-size: 3vh;
    }

   .section5 .fm-tag-a {
       color:#fff;
       text-decoration:solid;
       padding:0 0.7em 2em;
       margin:0.6em 0;
       font-size: 3vh;
   }

   .section5 .copyright {
       margin:2em;
       font-size: 3vh;
       text-align: center;
   }


  /* section 5 */

@media (max-width:768px) {
    /* section 1 */

    .inft-nav {
        flex-direction: column;
        justify-content: flex-start;
        padding:0 1em 0 1em;
        letter-spacing: 0.5px;
        z-index: 1000;
    }

    .if-left .fl-text {
        font-size: 1.4rem;
        font-weight:bold;
    }

    .if-left .btn-bars {
        display: none;
    }

    .if-right {
        display: none;
    }

    .if-right .item {
        flex:1 1 100%;
        text-align: right;
        font-size: 1em;
        font-weight: 500;
        padding: 0.5em;
        color:white !important;
    }

    .inft-desc {
        padding:1em 0 0 0;
    }

    .inft-desc .desc-sl {
        font-size: 2rem;
        padding:auto;
    }    

    .inft-desc .desc-text-one,.desc-text-two {
        font-size: 1rem;
    }

    .inft-arrows .arrows-p {
        background-color: #357;
        padding:0.5em 1em;
    }
    
    .inft-arrows .arrows-p-size {
        font-size:1.5em;
    }

    /* section 1 */

    /* section 2 */

    .section2 {
        padding:0 1em 0 1em;
    }

    .sect2-desc-one .wwd-title {
        padding:1em 0 0 0;
    }

    .market-feature .item {
        flex:1 1 100%;
        flex-flow: column nowrap;
        margin: 1em 0 0 0;
    }

     /* section 2 */


     /* section 3 */

     .section3 {
         height: auto;
         padding: 0 1em 0 1em;
     }

     .sct3-one .testimonial-area {
         padding:1em 0;
     }

     .sct3-one .ta-desc {
         max-width: 100%;
         text-align: justify;
     }

     .sct3-two .testimonial-board {
         flex-direction: column;
         padding:1em;
     }    

     .sct3-two .testimonial-board .item {
         max-width: 100%;
         font-size: 3vh;
         padding:1.2em 0 1.2em 0;
     }

     /* section 3 */

     /* section 4 */

     .section4 {
         height: auto;
     }

     .section4 .sect4-one {
         margin:1em 0 1em 0;
         padding:2em 1em;
     }

     .sect4-one .s40-desc {
         max-width:100%;
         text-align: justify;
     }

     .section4 .gallery-item {
         flex-flow: column;
         padding: 0;
     }


     /* section 5 */

     .section5 .row5 {
         max-width: 100%;
     }

     .section5 .p-cols-1 {
        max-width: 90%;
        text-align: justify;
        font-size: 3vh;
        padding:1em;
    }

     .section5 .form-contact {
         flex:1 1 100%;
         max-width: 100%;
     }

     .section5 .form-desc {
         flex:1 1 100%;
         max-width: 100%;
     }
}
